<script>
import api from '@/api'
import { getToken } from '@/api/auth'
import Editor from '@/components/Editor'
import BatchUploadImg from '@/components/BatchUploadImg'

export default {
  inject: ['reload'],
  data () {
    const validateDownUrl = (rule, value, callback) => {
      value = value == undefined ? '' : value;
      let reg1 = /^http:\/\//i,
          reg2 = /^https:\/\//i,
          falg = value.match(reg1) || value.match(reg2);
          
      if(value == '') {
        callback();
      } else if (!falg) {
        callback(new Error('下载地址必须是"http://"或"https://"开头'));
      } else {
        callback();
      }
    };
    const validateIs_yuyue = (rule, value, callback) => {
      const flag = this.formValidate.and_url == '' || this.formValidate.and_url == undefined && this.formValidate.ios_url=='' || this.formValidate.ios_url== undefined;
      if(value == 0 && flag) {
        callback(new Error('未勾选预约，请填写下载地址'))
        this.$Message.warning('未勾选预约，请填写下载地址')
      } else {
        callback()
      }
      
    };
    const validateTags = (rule, value, callback) => {
      if(value != '') {
        const num = value.split(',').length;
        if(num < 3) {
          callback(new Error('关联标签必须填写两个以上标签'))
          this.$Message.warning('关联标签必须填写两个以上标签')
        } else {
          callback()
        }
      } else {
        callback(new Error('关联标签必须填写两个以上标签'))
      }
      
    };
    const validateIntroduce = (rule, value, callback) => {
      const content = this.$refs.editorConfig.editor1.txt.text().replace(/&nbsp;|\s/ig, '');
      if(content.length == 0 || content == '') {
        this.$Message.warning('请填写游戏内容介绍')
        callback(new Error('请填写游戏内容介绍'))
      } else {
        callback()
      }
    };
    return {
      formValidate: {
        id: '',
        type_category: '',
        is_groupid: '',
        name: '',
        fgame: '',
        title: '',
        state: '',
        original: '',
        keywords: '',
        description: '',
        game_score: '',
        platname: '',
        language: '',
        safetitle: [],
        common_id: '',
        and_ver: '',
        and_size: '',
        and_unit: '',
        and_url: '',
        and_lock: '',
        cooperation: '',
        is_down: '',
        ios_ver: '',
        ios_size: '',
        ios_unit: '',
        ios_url: '',
        pc_url: '',
        type: '',
        recom: '',
        fictitious_down: '',
        comment: '',
        tip: '',
        is_yuyue: '',
        p_permission: '',
        tags: '',
        check_updateTime: '0',
        icon: '',
        introduce: '',
        journal: [],
        gameImageIds: ''
      },
      formArr: {
        ag_language: [],
        game_recom: [],
        game_type: [],
        pack_unit: [],
        safetitleList: [],
        tags: [],
        gameImageList: []
      },
      ruleValidate: {
        name: [
            { required: true, message: '请输入游戏名称', trigger: 'blur' }
        ],
        fgame: [
            { required: true, message: '选择主库游戏', trigger: 'blur' }
        ],
        title: [
            { required: true, message: '请输入网页SEO标题', trigger: 'blur' }
        ],
        keywords: [
            { required: true, message: '请输入网页SEO关键词', trigger: 'blur' }
        ],
        comment: [
            { required: true, message: '请输入小编点评', trigger: 'blur' }
        ],
        introduce: [
            { validator: validateIntroduce, trigger: 'blur' }
        ],
        type: [
            { required: true, message: '请选择游戏类型', trigger: 'change' }
        ],
        is_yuyue: [
            { validator: validateIs_yuyue, trigger: 'change' }
        ],
        and_url: [
            { validator: validateDownUrl, trigger: 'blur' }
        ],
        ios_url: [
            { validator: validateDownUrl, trigger: 'blur' }
        ],
        pc_url: [
            { validator: validateDownUrl, trigger: 'blur' }
        ],
        tags: [
            { validator: validateTags, trigger: 'blur' }
        ],
      },
      curIconFile: {},
      curIconUrl: '',
      curImgMsg: {name:'未选择任何图片'},
      preImgMsg: {},
      visible: false,
      // uploadList: [],
      token: getToken(),
      openTagAdd: false,
      relevanceTagList: {},
      getRelevanceTagParams: {},
      checkNameStatus: '',
      checkGameStatus: '',
      keywordList: [],
      batchUploadImgConfig: {//图片批量上传配置
        url: 'fileUpload',
        maxSize: '',
        maxNum: 300,
        type: '',
        sort: 1
      },
      formVideoData: {
        video: '',
        minute: '',
        second: '',
        video_cover: '',
      },
      oldvideo: '',
      curVideoCoverFile: {},
      curVideoCoverUrl: '',
      curVideoCoverMsg: {name:'未选择任何文件'},
      curMp4File: {},
      curMp4Msg: {name:'未选择任何文件'},
      is_down_first: '',
      isSearchFGame: false,//主库游戏自动填充搜索判断
      discussArr: ['','','','',''],
      remark: ''
    }
  },
  mounted () {
    const token = getToken(),
          gameid = this.$route.query.gameid,
          params = {token, gameid};

    api.editgame(params).then(res => {
      if(res.code == 0) {
        // console.log(res)
        const {id='', name, fgame, title, state, original, keywords, description, game_score, platname='', language, safetitle, common_id, is_down, type, recom, fictitious_down, comment, cooperation, tip, discuss, is_yuyue, p_permission, tags='', icon, introduce, journal, video, minute, second, video_cover, status} = res.data.gameInfo;
        const packInfo = res.data.packInfo || {};
        const {and_ver='', and_size='', and_unit='1', and_url='', and_lock='', ios_ver='', ios_size='', ios_unit='1', ios_url='', pc_url='', } = packInfo;
        let {ag_language, game_recom, game_type, pack_unit} = res.data.config;
        let gameImageList = res.data.gameImageList;
        let gameImageIds = res.data.gameImageIds;
        this.curIconUrl = icon;
        this.oldvideo = video;
        this.curVideoCoverUrl = video_cover;
        this.is_down_first = is_down;
        const safeTitleArr = safetitle ?  safetitle.split(',') : [];
        this.remark = status == 4 ? res.data.review.remark == '' ? '未给出原因' : res.data.review.remark : '';
        this.formValidate =  {
          id,
          type_category: res.data.type_category,
          groupid: res.data.groupid,
          is_groupid: res.data.is_groupid,
          name,
          fgame,
          title,
          state,
          original,
          keywords,
          description,
          game_score,
          platname,
          language,
          safetitle: safeTitleArr,
          common_id,
          and_ver,
          and_size,
          and_unit,
          and_url,
          and_lock,
          cooperation,
          is_down,
          ios_ver,
          ios_size,
          ios_unit,
          ios_url,
          pc_url,
          type,
          recom,
          fictitious_down,
          comment,
          tip,
          is_yuyue,
          p_permission,
          tags,
          check_updateTime: '0',
          icon,
          introduce,
          journal,
          gameImageIds
        };//表单基础数据传值
        // console.log(this.formValidate)
        this.formArr = {
          ag_language: ag_language,
          game_recom: game_recom,
          game_type: game_type,
          pack_unit: pack_unit,
          safetitleList: res.data.config.safetitle,
          tags: res.data.tagInfo || [],
          gameImageList,
        };//列数据传值
        this.formVideoData = {
          video,
          minute,
          second,
          video_cover,
        }//视频传值

        if(discuss && discuss.length>0) {
          discuss.forEach((ele,idx) => {
            this.discussArr[idx] = ele
          })
        }//评论传值

        // console.log(this.formValidate, this.formArr)

      } else {
        this.$Notice.error({
          desc: res.msg
        })
      }
    })
  },
  methods: {
    handleSubmit (params) {
      this.$Spin.show();
      api.exeEditGame(params).then(res => {
        this.$Spin.hide();
        if(res.code == 0) {
          const timer = setTimeout(() => {
            this.$Modal.remove();
            this.$router.push({
              name: 'gameList',
            });
          },5000)
          this.$Modal.confirm({
            title: '操作成功',
            content: '5秒后自动跳转列表页',
            cancelText: '',
            okText: '确认',
            loading: true,
            onCancel: () => {
              clearTimeout(timer)
            },
            onOk: () => {
              clearTimeout(timer)
              this.$Modal.remove();
              this.$router.push({
                name: 'gameList',
              });
            }
          });
          //游戏或应用发生修改上传local提示游戏或应用列表重新渲染
          localStorage.setItem('isRenderGameList', true)
        } else {
          this.$Message.error(res.msg)
        }
      })
    },//表单数据提交
    handleSubmitCheck (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const token = getToken(),
                gameid = this.formValidate.id,
                type_c = 2,
                classify = 1,
                realgame = this.formValidate.fgame,
                safetitle = this.formValidate.safetitle.join(','),
                icon_pack = this.formValidate.icon,
                {name,title,keywords,description,platname,type,original,cooperation,recom,state,language,fictitious_down,p_permission,comment,tip,game_score,tags,and_ver,and_size,and_url,ios_ver,ios_size,ios_url,pc_url,and_unit,ios_unit,and_lock,is_yuyue,is_down,common_id,check_updateTime,gameImageIds} = this.formValidate;

          const fr = new FormData();
                fr.append("token", token);
                fr.append("gameid", gameid);
                fr.append("type_c", type_c);
                fr.append("classify", classify);
                fr.append("realgame", realgame);
                fr.append("safetitle", safetitle);
                fr.append("icon_pack", icon_pack);
                fr.append("name", name);
                fr.append("title", title);
                fr.append("keywords", keywords);
                fr.append("description", description);
                fr.append("platname", platname);
                fr.append("type", type);
                fr.append("original", original);
                fr.append("cooperation", cooperation);
                fr.append("recom", recom);
                fr.append("state", state);
                fr.append("language", language);
                fr.append("fictitious_down", fictitious_down);
                fr.append("p_permission", p_permission);
                fr.append("comment", comment);
                fr.append("tip", tip);
                fr.append("game_score", game_score);
                fr.append("tags", tags);
                fr.append("and_ver", and_ver);
                fr.append("and_size", and_size);
                fr.append("and_url", and_url);
                fr.append("ios_ver", ios_ver);
                fr.append("ios_size", ios_size);
                fr.append("ios_url", ios_url);
                fr.append("pc_url", pc_url);
                fr.append("and_unit", and_unit);
                fr.append("ios_unit", ios_unit);
                fr.append("and_lock", and_lock);
                fr.append("is_yuyue", is_yuyue*1);
                fr.append("is_down", is_down);
                fr.append("discuss[]", this.discussArr[0]);
                fr.append("discuss[]", this.discussArr[1]);
                fr.append("discuss[]", this.discussArr[2]);
                fr.append("discuss[]", this.discussArr[3]);
                fr.append("discuss[]", this.discussArr[4]);
                fr.append("common_id", common_id);
                fr.append("check_updateTime", check_updateTime);
                fr.append("gameImageIds", gameImageIds);
                fr.append("clipPic", this.getClipPic());
                fr.append("icon", this.curIconFile);
                fr.append("introduce", this.getIntroduce());
                fr.append("journal1", this.getJournal1());
                fr.append("journal2", this.getJournal2());
                fr.append("video", this.formVideoData.video);
                fr.append("minute", this.formVideoData.minute);
                fr.append("second", this.formVideoData.second);
                fr.append("local_video", this.curMp4File);
                fr.append("cover", this.curVideoCoverFile);
                fr.append("oldvideo", this.oldvideo);
                fr.append("is_down_first", this.is_down_first);

          const params = fr;
          
          this.handleCheckName().then(res => {
            if(res.check) return this.handleCheckGame()
          }).then(res => {
            if(res.check) return this.checkImageArrayIllegalUrl(this.getIntroduce(), 2)
          }).then(res => {
            if(res.check) this.handleSubmit(params)
          })
        } else {
          this.$Message.warning('请完善表单内容')
        }
      })
    },//表单数据提交校验
    handleCheckName () {
      return new Promise((resolve) => {
        const token = getToken(),
              name = this.formValidate.name,
              type = 'game',
              id = this.formValidate.id,
              params = {token, name, type, id};

        api.checkNameXin(params).then(res =>{
          if(res.code == 0) {
            if(res.data[0] == -1) {
              resolve({check: true})
            } else {
              this.$Message.error('游戏名称已存在')
            }
          } else {
            this.$Message.error(res.msg)
          }
        })
      })
    },//表单数据提交之前检测游戏名称是否存在
    handleCheckGame () {
      return new Promise((resolve) => {
        const token = getToken(),
              name = this.formValidate.fgame,
              type = 'game',
              params = {token, name, type};

        api.checkGame(params).then(res =>{
          if(res.code == 0) {
            if(res.data.status == 1) {
              resolve({check: true})
            } else {
              this.$Message.error('主库游戏不存在，请先添加')
            }
          } else {
            this.$Message.error('主库游戏不存在，请先添加')
            this.$Message.error(res.msg)
          }
        }).catch(err => {
          this.$Message.error('主库游戏不存在，请先添加')
          this.$Message.error(err)
        })
      })
    },//表单数据提交之前检测主库游戏是否存在
    checkImageArrayIllegalUrl (html, type=1) {
      return new Promise(resolve => {
        const configDomainUrl = this.$store.state.configDomainUrl,
              imgReg = /<img.*?(?:>|\/>)/gi,
              srcReg = /src=[\\'\\"]?([^\\'\\"]*)[\\'\\"]?/i,
              imgArr = html.match(imgReg);

        if (imgArr && imgArr.length>0) {
          imgArr.forEach((ele) => {
            let src = ele.match(srcReg);
            if (src[1]) {
              let img_src = src[1],
                  flag1 = img_src.indexOf("http") != -1 || img_src.indexOf("https") != -1,
                  flag2 = img_src.indexOf(`${configDomainUrl.sendMainUrl}`) == -1,
                  flag3 = img_src.indexOf(`${configDomainUrl.rootMainUrl}`) != -1;

              if(flag1) {
                if(flag2) {
                  if(flag3) {
                    resolve({check:true})
                  } else {
                    type == 1 ? this.$Message.warning("文章正文内容有其它域名的图片") : this.$Message.warning("游戏介绍正文内容有其它域名的图片")
                    resolve({check:false})
                  }
                } else {
                  this.$Message.warning("不要上传内网域名的图片")
                  resolve({check:false})
                }
              }
            } else {
              resolve({check:true})
            }
          })
        }else{
          resolve({check:true})
        }
      })
    },//表单数据提交之前检测游戏介绍外链图片是否存在
    getClipPic () {
      // console.log(this.$refs.batchUploadImgContent.waitUploadImgPreviewList)
      let clipPicArr = this.$refs.batchUploadImgContent.waitUploadImgPreviewList,
            uploadList = '';
      clipPicArr.forEach((ele,idx) => {
        if(ele.uploaded == 1) {
          if (uploadList == '') {
            uploadList = 'WU_FILE_' + idx + '-' + ele.trueUrl;
          } else {
            uploadList = uploadList + ',' + 'WU_FILE_' + idx + '-' + ele.trueUrl;
          }
        }
      })
      // console.log(uploadList)
      return uploadList
    },//截图上传数据转换
    getIntroduce () {
      const content = this.$refs.editorConfig.editor1.txt.html()
      // console.log(content)
      return content
    },//游戏介绍上传数据转换
    getJournal1 () {
      const content = this.$refs.editorDataConfig.editor2.txt.html()
      // console.log(content)
      return content
    },//日志上传数据转换
    getJournal2 () {
      const content = this.$refs.editorDataHistoryConfig.editor2.txt.html()
      // console.log(content)
      return content
    },//历史日志上传数据转换
    getFile (e) {
      const file = e.target.files[0],
            type = file.type,
            limitType = 'jpg|jpeg|png',
            limitTypeReg = new RegExp('.('+limitType+')$');

      if (!limitTypeReg.test(type)) {
        this.$Message.warning(`请传${limitType}图片`)
      } else {
        this.curIconFile = e.target.files[0];
        this.curImgMsg = e.target.files[0];
        let fr = new FileReader();
        fr.onload = () => {
          this.curIconUrl = fr.result
        };
        fr.readAsDataURL(this.curImgMsg)
      }
    },//icon file获取
    handleView (ele) {
      this.preImgMsg.name = ele.path.split('/').reverse()[0];
      this.preImgMsg.href = ele.path
      this.preImgMsg.addtime = this.$moment(ele.addtime*1000).format("YYYY-M-DD k:mm")
      this.visible = true;
    },//截图预览
    handleRemoveGameImg (index) {
      this.formArr.gameImageList.splice(index, 1)
      let curGameImageIds = ''
      this.formArr.gameImageList.forEach(ele => {
        curGameImageIds += (ele.id+',')
      })
      this.formValidate.gameImageIds = curGameImageIds;
      // console.log(this.formArr.gameImageList, curGameImageIds)
    },//截图删除
    setOpenTagAdd () {
      this.openTagAdd = true
      const token = getToken(),
            page = 1,
            per_page = 40,
            classify = 1,
            selval = '',
            id = '',
            params = {token, page, per_page, classify, selval, id};
      
      this.getRelevanceTagParams = params;
      this.getRelevanceTag();
    },//获取关联标签列表
    getRelevanceTag (flag) {
      let params;
      if(flag) {
        this.getRelevanceTagParams.page = 1
        params = this.getRelevanceTagParams
      } else {
        params = this.getRelevanceTagParams
      }
      
      api.searchAllTagList(params).then(res => {
        if(res.code == 0) {
          console.log(res)
          this.relevanceTagList = res.data
        } else {
          this.$Notice.error({
            desc: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({
          desc: err
        })
      })
    },//获取关联标签执行函数
    addRelevanceTag (item) {
      let isHave = this.formValidate.tags.indexOf(item.id) == -1;
      if(isHave) {
        this.formValidate.tags+=(','+item.id)
        this.formArr.tags.push({id:item.id, name: item.name})
        this.$Message.success('关联添加成功')
        // console.log(this.formValidate.tags)
      } else {
        this.$Message.warning('当前关联标签id已经添加，请不要重复添加')
      }
    },//关联标签添加
    delRelevanceTag (id, idx) {
      console.log(this.formValidate.tags,id, idx)
      const reg = new RegExp(id+',?');
      this.formValidate.tags = this.formValidate.tags.replace(reg, '');
      this.formArr.tags.splice(idx, 1)
      // console.log(id, idx)
      console.log(this.formValidate.tags)
    },//关联标签删除
    changePage (page) {
      this.getRelevanceTagParams.page = page*1;
      this.getRelevanceTag();
    },//关联标签列表分页
    changePageNum (num) {
      this.getRelevanceTagParams.per_page = num*1;
      this.getRelevanceTag();
    },//关联标签列表每页显示数量
    checkName () {
      const token = getToken(),
            name = this.formValidate.name,
            type = 'game',
            id = this.formValidate.id,
            params = {token, name, type, id};

      api.checkName(params).then(res =>{
        if(res.code == 0) {
          if(res.data[0] == -1) {
            this.checkNameStatus = 'ok'
          } else if(res.data[0] == 1) {
            this.checkNameStatus = '已存在'
          } else {
            this.checkNameStatus = '其他编辑正在录入...'
          }
          console.log(this.checkNameStatus)
        } else {
          this.$Message.error(res.msg)
        }
      })
    },//游戏名称校验
    autoKeyword () {
      const query = this.formValidate.fgame
      if(query != '') {
        const token = getToken(),
            name = query,
            params = {token, name};
         
        api.backGame(params).then(res => {
          if(res.code == 0) {
            if(res.data.length>0) {
              this.keywordList = res.data
              this.isSearchFGame = true;
            } else {
              this.isSearchFGame = false;
            }
          }else {
            this.isSearchFGame = false;
            this.$Notice.error({
              desc: res.msg
            })
          }
        })
      }
    },//主库游戏自动填充
    checkGame () {
      this.isSearchFGame = false
      const token = getToken(),
            name = this.formValidate.fgame,
            type = 'game',
            params = {token, name, type};

      api.checkGame(params).then(res =>{
        if(res.code == 0) {
          this.checkGameStatus = res.data.status == 1 ? '该游戏关联成功' : '该游戏不存在';
        } else {
          this.$Message.error(res.msg)
        }
      })
    },//主库游戏名称校验
    setKeyword (name) {
      this.formValidate.fgame = name
    },//主库游戏名称选中
    addGame () {
      const token = getToken(),
            name = this.formValidate.fgame,
            type = 'game',
            params = {token, type, name};

      api.addGame(params).then(res => {
        if(res.code == 0) {
          this.$Message.success('添加成功')
          this.checkGame()
        } else {
          this.$Message.error(res.msg)
        }
      })      
    },//主库游戏添加
    clearGameNameStatus (type) {
      type == 1 ? this.checkNameStatus = '' : this.checkGameStatus = '';
    },//游戏、主库游戏检测状态清除
    downLongRangeImg () {
      let curEditorContent = this.$refs.editorConfig.getCurEditorContent();

      const token = getToken(),
            content = curEditorContent,
            type = 1,
            params = {token, content, type};

      api.xiazai(params).then(res => {
        if(res.code == 0) {
          this.$Message.success(`下载完成${res.data.count}图片`)
        } else {
          this.$Message.error(res.msg)
        }
      }).catch(err => {
        this.$Message.error(err)
      })
    },//一键下载到本地
    addImgWater () {
      let curEditorContent = this.$refs.editorConfig.getCurEditorContent();

      const token = getToken(),
            content = curEditorContent,
            type = 1,
            params = {token, content, type};

      api.shuiyin(params).then(res => {
        if(res.code == 0) {
          this.$Message.success(`完成水印${res.data.count}张图`)
        } else {
          this.$Message.error(res.html)
        }
      }).catch(err => {
        this.$Message.error(err)
      })
    },//一键加水印
    commonLibDetail () {
      const token = getToken(),
            commonid = this.formValidate.common_id*1,
            params = {token, commonid};
      api.commonLibDetail(params).then(res => {
        if(res && res.length>0) {
          this.formValidate.and_ver = res.version
          this.formValidate.and_size = res.size
          this.formValidate.and_url = res.downurl
        } else {
          this.$Message.warning('暂无下载包')
        }
      })
    },//公共库id获取
    getVideoCover (e) {
      if(e.target.files[0]) {
        const file = e.target.files[0];
        const type = file.type,
              limitType = 'jpg|jpeg|png',
              limitTypeReg = new RegExp('.('+limitType+')$');

        if (!limitTypeReg.test(type)) {
          this.$Message.warning(`请传${limitType}图片`)
        } else {
          this.curVideoCoverFile = file;
          this.curVideoCoverMsg = file;
          let fr = new FileReader();
          fr.onload = () => {
            this.curVideoCoverUrl = fr.result
          };
          fr.readAsDataURL(this.curVideoCoverMsg)
        }
      }
    },
    getMp4 (e) {
      if(e.target.files[0]) {
        this.curMp4File = e.target.files[0];
        let file = e.target.files[0];
        const typeLimit = file.type.indexOf('mp4');
        console.log(typeLimit)
        if(typeLimit == -1) {
          this.$Message.warning('视频格式只能是mp4')
        } else {
          this.curMp4Msg = e.target.files[0];
        }
      }
    },
  },
  components: {Editor, BatchUploadImg}
}
</script>
<template>
