        </FormItem>
        <FormItem label="公共库ID" prop="common_id" class="big">
          <Input v-model="formValidate.common_id" clearable class="small"></Input>
          <Button type="info" ghost style="marginLeft: 10px" @click="commonLibDetail">一键获取</Button>
        </FormItem>
        <div class="formBox">
          <FormItem label="安卓版本号" prop="and_ver"  class="small">
            <Input v-model="formValidate.and_ver" clearable ></Input>
          </FormItem>
          <FormItem label="安卓包大小" prop="and_size"  class="small">
            <Input v-model="formValidate.and_size" clearable ></Input>
