            :maxlength=300
            show-word-limit
            type="textarea"
            :rows="4"
            style="margin: 0 6px 6px 0;width:19%" 
            placeholder="评论不能字数超过300"
          />
          <Input
            v-model="discussArr[2]"
            :value="discussArr[2]" 
            :maxlength=300
            show-word-limit
            type="textarea"
            :rows="4"
            style="margin: 0 6px 6px 0;width:19%"
            placeholder="评论不能字数超过300"
          />
          <Input
            v-model="discussArr[3]"
            :value="discussArr[3]" 
            :maxlength=300
            show-word-limit
            type="textarea"
            :rows="4"
            style="margin: 0 6px 6px 0;width:19%"
            placeholder="评论不能字数超过300"
