  }
  .uploadBox {
      display: inline-block;
      width: 100px;
      height: 34px;
      border-radius: 4px;
      border: 1px dashed #ccc;
      position: relative;
      input {
        opacity: 0;
        width: 100px;
        height: 32px;
      }
      .upload {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        padding: 0 4px 0 12px;
