    border: 1px dashed #ccc;
  }
  .iconBox {
    padding-bottom: 20px;
  }
  .uploadlistBox{
    background: #fff;
    border: 1px dashed #dcdee2;
    border-radius: 4px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: border-color .2s ease;
    padding: 20px 20px 20px;
    &:hover {
      border: 1px dashed #2d8cf0;
    }
    .upload-list{
      display: inline-block;
      height: 144px;
      line-height: 144px;
      border-radius: 4px;
      overflow: hidden;
      position: relative;
      margin-right: 6px;
      text-align: center;
      box-shadow: 0 1px 1px #00000020;
      &:hover .upload-list-cover {
        display: block;
      }
      img{
        width: 100%;
        height: 100%;
      }
      .upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
        i{
          color: #fff;
          font-size: 20px;
          cursor: pointer;
          margin: 0 2px;
        }
      }
    }
    .uploadedIcon, .uploadedIconError {
      position: absolute;
      bottom: 2px;
      right: 6px;
      font-size: 26px;
      color: #fff;
      z-index: 1;
      &::after {
        content: '';
        position: absolute;
        top: -32px;
        left: -28px;
        border-width: 30px;
        border-style: solid;
        border-radius: 0 0 4px 0;
        border-color: transparent green green transparent;
        z-index: -1;
